export default {
	data() {
		return {
			content:''
		};
	},
	onLoad(options) {
		this.content=options.id;
	},
	methods: {
		sub() {
			if (this.content != '') {
				let parmas = {
					uid: getApp().globalData.uid,
					oneSinge: this.content
				};
				this._reqw
					.ipost(parma, 'edmitPersonInfo')
					.then(res => {
						res.result == 0
							? ((this.$api.prePage().dataObject.oneSinge = this.content),
							  this.$api.tip('修改成功!'),
							  setTimeout(() => {
									this.$api.back();
							  }, 300))
							: this.$api.tip(res.resultNote);
					})
					.catch(err => {});
			}
		}
	}
};
